<template>
  <Modal ref="modal" :title="title" @modal:close='done'>
    <form>
      <fieldset>
        <div class="mb-3">
          <label class="form-label">produit</label>
          <input type="text" class="form-control" v-model="product.name">
        </div>
        <div class="mb-3">
          <label class="form-label">description</label>
          <input type="text" class="form-control" v-model="product.desc">
        </div>
        <div class="mb-3">
          <div class="form-check form-check-inline">
            <input class="form-check-input" type="checkbox" v-model="product.highlight">
            <label class="form-check-label">mettre en avant</label>
          </div>
          <div class="form-check form-check-inline">
            <input class="form-check-input" type="checkbox" v-model="product.available">
            <label class="form-check-label">disponible?</label>
          </div>
        </div>
      </fieldset>
      <fieldset>
        <legend class="fs-4">Categories</legend>
        <div class="mb-3" v-for="(c, i) in product.categories" :key="i">
          <label class="form-label">categorie</label>
          <input type="text" class="form-control" :value="c">
        </div>
      </fieldset>
      <fieldset>
        <legend class="fs-4">Prix</legend>
        <div class="mb-3" v-for="(p, i) in product.prices" :key="i">
          <div class="row">
            <label class="col col-form-label">prix</label>
            <div class="col">
              <input type="number" class="form-control" :value="price">
            </div>
            <div class="col">
              <input type="number" class="form-control" min="1" :value="quantity">
            </div>
            <div class="col">
              <input type="text" class="form-control" :value="unit">
            </div>
          </div>
        </div>
      </fieldset>
    </form>
  </Modal>
</template>
<script>
import Modal from '../components/Modal.vue'
import {mapGetters} from 'vuex'

const model = {
  name: "",
  desc: "",
  prices: [],
  categories: [],
  available: false,
  highlight: false,
}

export default {
  name: 'EditProduct',
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.resetDefault()
      vm.toggle()
    })
  },
  data() {
    return {
      product: {},
    }
  },
  computed: {
    ...mapGetters('catalog', ['products']),
    editMode() {
      return this.$route.name == 'edit.product'
    },
    title() {
      return this.editMode ? `détail de ${this.product.name}` : 'créer un nouveau produit'
    }
  },
  methods: {
    toggle() {
      this.$refs.modal.toggle()
    },
    done() {
      this.$router.go(-1)
    },
    resetDefault() {
      if (!this.$route.params.id) {
        this.product = Object.assign({}, model)
        return
      }
      this.product = Object.assign({}, this.products[this.$route.params.id])
    },
  },
  components: {
    Modal,
  },
}
</script>
